import React from "react";
import { useState, useEffect } from "react";
import { Container, Form, Row, Col, Card } from "react-bootstrap";

import axios from "axios";
import MoboModals from "./modals/MoboModals";
import Ram from "./Ram";

const Motherboard = ({ socket }) => {
  const [datas, setDatas] = useState([]);
  const [data, setData] = useState("");
  const [showModals, setShowModals] = useState(false);
  const [showRam, setShowRam] = useState("d-none");
  const [memory, setMemory] = useState("");
  const [m2, setM2] = useState("");
  const [currentId, setCurrentId] = useState(0);
  const [motherboardPrice, setMotherboardPrice] = useState("");

  const handleClose = () => setShowModals(false);
  const handleShow = (id) => {
    setShowModals(true);
    setData(datas[id]);
  };
  function cardBorder(id) {
    var element = document.getElementById(`motherboard${id}`);
    var currentElement = document.getElementById(`motherboard${currentId}`);
    setCurrentId(id);
    element.classList.remove("border-0");
    currentElement.classList.add("border-0");
    if (currentId === id) {
      element.classList.remove("border-0");
    }
  }

  const cardBordere = () => {};

  useEffect(() => {
    if (socket === "" || socket === null) {
    } else {
      const getDatas = async () => {
        // const url = process.env.REACT_APP_API_URL;
        const url = "https://rakitpcapi.up.railway.app/api/v1";
        const response = await axios.get(`${url}/motherboard/socket/${socket}`);
        setDatas(response.data);
        setShowRam("d-none");
      };
      getDatas();
    }
  }, [socket]);

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Motherboard</div>
      <Form>
        <div className="motherboard">
          <Row className=" justify-content-md-center">
            {datas.map((data, index) => (
              <Col lg="2" key={index} className="mb-3">
                <Card
                  className="card-motherboard border-0"
                  id={`motherboard${data.id}`}
                  onClick={() => {
                    setShowRam("d-block");
                    setMemory(data.memory);
                    cardBorder(data.id);
                    setMotherboardPrice(data.motherboardPrice);
                    setM2(data.m2Slot);
                  }}
                >
                  <Card.Img
                    variant="none"
                    src={data.motherboardUrl}
                    alt={data.motherboardName}
                  />
                  <Card.Text
                    className="motherboard-text"
                    id={data.motherboardName}
                  >
                    <div className="motherboard-title">
                      {data.motherboardName}
                    </div>
                    <div className="motherboard-price text-blue">
                      Rp. {numberWithCommas(data.motherboardPrice)}
                    </div>
                  </Card.Text>
                </Card>
                <Card className="btn-card" onClick={() => handleShow(index)}>
                  <div className="btn-more">Spesifikasi ...</div>
                </Card>
              </Col>
            ))}
            <Card className="d-none" id="motherboard0" />
          </Row>
        </div>
      </Form>
      <MoboModals data={data} handleClose={handleClose} show={showModals} />
      <div className={showRam}>
        <Container>
          <div className="pt-2 text-bold">
            Price : Rp. {numberWithCommas(motherboardPrice)}
          </div>
        </Container>
        <Ram memory={memory} m2={m2} />
      </div>
    </div>
  );
};

export default Motherboard;

export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
