import React from "react";
import Vendor from "../components/Vendor";
import { useEffect, useState } from "react";

export const Compatible = ({ utamaPosition }) => {
  const [offsetTop, setOffsetTop] = useState(0);
  const [offsetHeight, setOffsetHeight] = useState(0);
  useEffect(() => {
    const noted = document.getElementById("utama");
    const getTop = noted.offsetTop + 80;
    const getHeight = noted.offsetHeight;
    setOffsetTop(getTop);
    setOffsetHeight(getTop + getHeight);
  }, []);
  utamaPosition(offsetTop, offsetHeight);

  return (
    <div className="komponen" id="utama">
      <div className=" fw-bold fs-4 pt-4">Komponen Utama</div>
      <div className="container">
        <Vendor />
      </div>
    </div>
  );
};
