import React from "react";
import { useState, useEffect } from "react";
import { Container, Form } from "react-bootstrap";
import axios from "axios";
import Select from "react-select";

const Storage = ({ m2 }) => {
  const [options, setOptions] = useState("");
  const [showCassing, setShowCassing] = useState("d-none");
  const [storagePrice, setStoragePrice] = useState("");

  useEffect(() => {
    if (m2 !== "-") {
      const getDatas = async () => {
        const arr = [];
        // const url = process.env.REACT_APP_API_URL;
        await axios.get(`http://localhost:5000/api/v1/storage`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.name + data.size,
              label: data.name,
              url: data.url,
              size: data.size,
              type: data.type,
              slotType: data.slotType,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowCassing("d-none");
      };
      getDatas();
    } else {
      const getDatas = async () => {
        const arr = [];
        // const url = process.env.REACT_APP_API_URL;
        const url = "https://rakitpcapi.up.railway.app/api/v1";
        await axios.get(`${url}/storage/slotType/sata`).then((res) => {
          let result = res.data;
          result.map((data) => {
            return arr.push({
              value: data.name + data.size,
              label: data.name,
              url: data.url,
              size: data.size,
              type: data.type,
              slotType: data.slotType,
              price: data.price,
            });
          });
        });
        setOptions(arr);
        setShowCassing("d-none");
      };
      getDatas();
    }
  }, [m2]);

  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Storage</div>
      <Form>
        <Container>
          <Select
            className="basic-single"
            classNamePrefix="select"
            placeholder="Pilih RAM"
            isClearable={true}
            isSearchable={true}
            options={options}
            formatOptionLabel={(storage) => (
              <div className="country-option d-flex">
                <div className=" float-start">
                  <img width={"60px"} height={"60px"} src={storage.url} />{" "}
                  &nbsp;
                </div>
                <div className=" float-end">
                  {storage.label} {storage.size} {storage.type} -{" "}
                  {storage.slotType} <br />
                  <span className=" text-blue fst-italic">
                    Rp. {numberWithCommas(storage.price)}
                  </span>
                </div>{" "}
              </div>
            )}
            name="processor"
            onChange={(e) => {
              setShowCassing("d-block");
              setStoragePrice(e.price);
            }}
          />
        </Container>
      </Form>
      <div className={showCassing}>
        <Container>
          <div className="pt-2 text-bold">
            Price : Rp. {numberWithCommas(storagePrice)}
          </div>
        </Container>
      </div>
    </div>
  );
};

export default Storage;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
