import { BrowserRouter, Route, Routes } from "react-router-dom";
import Header from "./components/Header";
import Produk from "./components/Produk";
import Home from "./pages/Home";
import Rekomendasi from "./pages/Rekomendasi";
import Simulasi from "./pages/Simulasi";

function App() {
  return (
    <BrowserRouter>
      <Header />

      <Routes>
        <Route path="/" element={<Home />} exact></Route>
        <Route path="/rakitpc" element={<Home />} exact></Route>
        <Route path="/simulasi" element={<Simulasi />} exact></Route>
        <Route path="/rakomendasi" element={<Rekomendasi />} exact></Route>
        <Route
          path="rekomendasi/:kodeProduk"
          element={<Produk />}
          exact
        ></Route>
      </Routes>
    </BrowserRouter>
  );
}

export default App;
