import React from "react";
import Note from "../components/Note";
import { useRef, useState, useEffect } from "react";
import { Compatible } from "../mainComponent/Compatible";
import { OnThisPage } from "../components/OnThisPage";
import CardProduct from "../mainComponent/CardProduct";
import Optional from "../mainComponent/Optional";
import Tambahan from "../mainComponent/Tambahan";

const Simulasi = () => {
  const [offset, setOffset] = useState(0);
  const [active, setActive] = useState("");
  const noteRef = useRef(null);
  const vendorRef = useRef(null);
  const moreRef = useRef(null);
  const accRef = useRef(null);
  const handleClick = (ref) => {
    window.scrollTo({
      top: ref.offsetTop - 70,
      left: 0,
      behavior: "smooth",
    });
  };

  const notePosition = (top, height) => {
    if (offset >= top && offset <= height) {
      setActive("note");
    }
  };
  const utamaPosition = (top, height) => {
    if (offset >= top && offset <= height) {
      setActive("utama");
    }
  };

  useEffect(() => {
    const onScroll = () => setOffset(window.pageYOffset);
    // clean up code
    window.removeEventListener("scroll", onScroll);
    window.addEventListener("scroll", onScroll, { passive: true });
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <>
      <div className=" pt-4 shadow-blur"></div>
      <div className=" mx-8">
        <div className="row">
          <div className="col-2 h-100">
            <OnThisPage
              active={active}
              handleClick={handleClick}
              vendorRef={vendorRef}
              noteRef={noteRef}
              moreRef={moreRef}
              accRef={accRef}
            />
          </div>
          <div className="col-10 body mt-6 py-4">
            <CardProduct />
          </div>
          <div className="col-2"></div>
          <div className="col-10 body mt-3 py-4">
            <div ref={noteRef}>
              <Note notePosition={notePosition} />
            </div>
            <div ref={vendorRef}>
              <Compatible utamaPosition={utamaPosition} />
            </div>
            <div ref={moreRef}>
              <Optional offset={offset} />
            </div>
            <div ref={accRef}>
              <Tambahan offset={offset} />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Simulasi;
