import React from "react";
import { Form, Row, Col, Card } from "react-bootstrap";
import { useState, useEffect } from "react";
import axios from "axios";
import Processor from "./Processor";

const Seri = ({ vendor }) => {
  const [type, setType] = useState("");
  const [show, setShow] = useState("d-none");
  const [data, setData] = useState([]);

  useEffect(() => {
    const getDatas = async () => {
      // const url = process.env.REACT_APP_API_URL;
      const url = "https://rakitpcapi.up.railway.app/api/v1";
      const response = await axios.get(`${url}/processor/vendor/${vendor}`);
      setData(response.data);
      setShow("d-none");
    };
    getDatas();
  }, [vendor]);

  const handleClick = (e) => {
    e.preventDefault();
    setShow("d-block");
    let chose = e.target.id;
    if (chose === null || chose === "") {
      chose = e.target.alt;
    }
    setType(chose);
  };
  return (
    <div className="text-black pt-3">
      <div className="title fw-bold mb-3">Pilih Seri Processor</div>
      <Form>
        <div className="seri">
          <Row className=" justify-content-md-center">
            {data.map((datas, index) => (
              <Col lg="2" key={index}>
                <Card
                  className="card-seri "
                  onClick={handleClick}
                  id={datas.type}
                >
                  <Card.Img variant="none" src={datas.url} alt={datas.type} />
                  <Card.Text id={datas.type}>{datas.name}</Card.Text>
                </Card>
              </Col>
            ))}
          </Row>
        </div>
      </Form>
      <div className={show}>
        <Processor type={type} />
      </div>
    </div>
  );
};

export default Seri;
